import axios from "axios";
export default {
  methods: {
    saveChanges: function saveChanges() {}
  },
  data: function data() {
    return {
      dialog: false,
      autoUpdate: true,
      isUpdating: false,
      snack: false,
      snackColor: "",
      snackText: "",
      valid: false,
      name: "",
      users: [],
      title: "",
      group: {
        name: "",
        description: "",
        users: [],
        usersAdmin: [],
        color: "#448AD1",
        textColor: "",
        created_by: ""
      },
      nameRules: [function (v) {
        return !!v || "This field is required";
      }, function (v) {
        return v.length >= 8 || "This field is required";
      }]
    };
  },
  created: function created() {
    var _this = this;

    var apiURL = "http://localhost:3000/api/group/".concat(this.$route.params.id);
    axios.get(apiURL, {
      headers: {
        token: localStorage.getItem("token")
      }
    }).then(function (res) {
      _this.group = res.data.group;
    });
  }
};