import Container from "./ChatContainer.vue";
import ConnectedUsers from "./ConnectedUsers.vue";
import { VueTyper } from "vue-typer";
import axios from "axios";
export default {
  name: "ChatPage",
  components: {
    Container: Container,
    VueTyper: VueTyper,
    ConnectedUsers: ConnectedUsers
  },
  computed: {
    user: function user() {
      return this.$store.state.user;
    }
  },
  data: function data() {
    return {
      snack: false,
      snackColor: "",
      snackText: "",
      //Username data
      username: "You",
      usernameSocket: "",
      userProfile: {},
      //Socket for connection
      socket: {},
      //All messages
      chat: [],
      //Message
      message: "",
      //Online user,
      onlineUsers: [],
      //Typing process
      isTyping: false,
      someoneTyping: false,
      lastTypingTime: new Date(),
      TYPING_TIMER_LENGTH: 400,
      messageRules: [function (v) {
        return !!v || "Message can't be empty";
      }]
    };
  },
  created: function created() {
    this.socket = this.$store.state.socket;
  },
  mounted: function mounted() {
    var _this = this;

    //LISTENERS OF THE SOCKET
    //Send a message
    this.socket.on("message", function (msg) {
      _this.chat.push(msg);
    }); // Whenever the server emits 'typing', show the typing message

    this.socket.on("typing", function (data) {
      console.log(data);
      _this.someoneTyping = true;
    });
    this.socket.on("login", function (data) {
      _this.usernameSocket = data.username;
    }); // Whenever the server emits 'stop typing', kill the typing message

    this.socket.on("stop typing", function (data) {
      console.log(data);
      _this.someoneTyping = false;
    }); //Add new user to the chat room

    this.socket.on("online users", function (users) {
      _this.onlineUsers = users;
    }); //disconnect of the chat room
  },
  watch: {
    // whenever question changes, this function will run
    message: function message() {
      this.updateTyping();
    }
  },
  methods: {
    sendMessage: function sendMessage() {
      var _this2 = this;

      if (this.message != "") {
        var message = {
          message: this.message,
          username: this.$store.state.user
        };
        axios.post(this.$store.state.apiURL + "/message/send-message", {
          message: message,
          group: this.$route.params.id
        }).then(function (res) {
          console.log(res.data);
          _this2.message = "";
        });
        /**
           * 
        this.socket.emit("message", this.message);
        this.chat.push({ username: this.username, message: this.message });
        this.message = "";
           */
      } else {
        this.snack = true;
        this.snackText = "Put some text pls";
        this.snackColor = "red";
      }
    },
    updateTyping: function updateTyping() {
      var _this3 = this;

      //TODO : put if is connected or not
      if (this.isTyping === false) {
        this.isTyping = true;
        this.socket.emit("typing");
        console.log("typing");
      }

      this.lastTypingTime = new Date().getTime();
      setTimeout(function () {
        var typingTimer = new Date().getTime();
        var timeDiff = typingTimer - _this3.lastTypingTime;

        if (timeDiff >= _this3.TYPING_TIMER_LENGTH && _this3.isTyping) {
          _this3.socket.emit("stop typing");

          _this3.isTyping = false;
        }
      }, this.TYPING_TIMER_LENGTH);
    }
  }
};